
section.showcase {
    margin: 60px 0 100px 0;

    header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        h2 {
            margin: 0 0 30px 0;
            text-transform: none;
            font-size: 1.75rem;
        }

        a {
            display: block;
            margin: 0 0 30px 0;
            font-size: 1.375rem;
            color: $cor-secundaria;
            transition: 0.2s ease-in-out;

            &:hover {
                color: $cor-primaria;
            }
        }
        
    }
}

section.showcase section.shelf {
    div.product {
        display: flex;
        flex-direction: column;
        margin: 15px;
        transition: 0.2s ease-in-out;
        border-radius: 20px;

        div.shadow {
            padding: 10px;
            transition: 0.2s ease-in-out;
            border-radius: 20px;

            div.product-image a img {
                width: 100%;
            }

            div.product-info {
                div.info-name a {
                    display: inline;
                    font-family: "Swiss 721 Medium";
                    font-size: 0.875em;
                    line-height: 1.1em;
                    color: $cor-escrita;
                }

                div.info-details {
                    text-align: center;
                    margin: 10px 0;

                    span {
                        display: block;
                        color: #C4C4C4;
                        font-family: "Swiss 721 Light";
                    }

                    span:first-child {
                        font-size: 0.75em;
                    }

                    span.units {
                        font-size: 0.875em;
                    }
                }

                div.info-price {
                    text-align: center;
                    color: $cor-primaria;
                    font-family: "Swiss 721 Bold";
                    font-size: 1.25em;
                    margin: 15px 0;
                }
            }

            button.product-get {
                width: 100%;
                height: 40px;
                border: 0;
                border-radius: 15px;
                background-color: $cor-acao;
                transition: 0.3s ease-in-out;

                &:hover {
                    background-color: $cor-primaria;
                }

                a {
                    display: block;
                    width: 100%;
                    height: 100%;
                    line-height: 40px;
                    font-size: 1.125em;
                    text-transform: uppercase;
                    font-family: "Swiss 721 Medium";
                    color: #FFF;
                }
            }
        }

        &:hover {
            box-shadow: 0 5px 6px 0 rgb(0 0 0 / 20%);

            .shadow {
                box-shadow: 0 3px 16px 0 rgb(0 0 0 / 12%);
            }
        }
    }
}

main.product-page section.showcase.related {
    margin: 100px 0 100px 0;
}

section.showcase.showcase-category {
    margin: 0 0 0 20px;
}


