main.page-category {
    section.gateway {
        margin: 30px 0 0 0;
        font-family: "Swiss 721 Bold";
        color: $cor-escrita;
    }
}

section.content-category {
    margin: 40px 0 100px 0;

    .shelf-page {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
    }

    section.filters {
        width: 100%;
        background: rgba($cor-box, 0.3);
        padding: 15px;

        .filtering .list-filter ul li {

            display: inline-flex;
            align-items: center;
            line-height: em(25);
            width: 100%;

            label {
                padding: 0 5px 0 8px 0;
                font-size: em(16);
                color: $cor-escrita;
                font-family: "Swiss 721 Roman";
            }

            strong a {
                color: $cor-box;
                font-family: "Swiss 721 Light";
                font-size: em(12);
                transition: 0.2s ease-in-out;

                &:hover {
                    text-decoration: underline;
                }
            }
        }

        .btn-view {
            display: block;
            padding: 10px 0;
            color: $cor-secundaria;
            font-size: em(15);
        }
    }
}

div.fix {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;

    div.per_page {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-right: 60px;

        span {
            margin-right: 15px;
            font-size: em(14);
        }

        .nice-select {
            width: 70px;
            background: $cor-primaria;
            color: #FFFFFF;
            border-radius: 15px;
            border: 0;

            .list .option {
                width: 70px;
                color: $cor-secundaria;

                &:hover {
                    background-color: rgba($cor-primaria, 0.6);
                    color: #FFFFFF;
                }
            }

            .list .option.disabled {
                opacity: 0.6;
            }

            .list .option.selected {
                background-color: rgba($cor-primaria, 0.4) !important;
            }

            .list .option.selected.focus {
                background-color: rgba($cor-primaria, 0.6);
                color: #FFFFFF;
            }

            &:after {
                border-color: #FFFFFF;
            }
        }
    }

    div.per_order {
        display: flex;
        flex-direction: row;
        align-items: center;
        

        .nice-select {
            width: 300px;
            background: $cor-primaria;
            color: #FFFFFF;
            border-radius: 15px;
            border: 0;

            .list .option {
                width: 300px;
                color: $cor-secundaria;

                &:hover {
                    background-color: rgba($cor-primaria, 0.6);
                    color: #FFFFFF;
                }
            }

            .list .option.disabled {
                opacity: 0.6;
            }

            .list .option.selected {
                background-color: rgba($cor-primaria, 0.4) !important;
            }

            .list .option.selected.focus {
                background-color: rgba($cor-primaria, 0.6);
                color: #FFFFFF;
            }

            &:after {
                border-color: #FFFFFF;
            }
        }

        span {
            margin-right: 15px;
            font-size: em(14);
        }
    }
}







    /* Medium Devices, Desktops */
    @media only screen and (min-width : 992px) {
        main.page-category section.content-category {
            .filters-mobile {
                display: none;
            }

            section.filters {
                display: block;
                
            }
        }
    }

    /* Large Devices, Wide Screens */
    @media only screen and (min-width : 1200px) {
        main.page-category section.content-category {
            
            .wraper-category {
                display: grid;
                grid-template-columns: 300px 1fr;
            }
        
            .shelf-page {
                display: grid;
                grid-template-columns: repeat(4, 1fr);
            }
        }
    }

    /*==========  Non-Mobile First Method  ==========*/

    /* Large Devices, Wide Screens */
    @media only screen and (max-width : 1200px) {
        main.page-category section.content-category {
            .wraper-category {
                display: grid;
                grid-template-columns: 280px 1fr;
            }
            
            .shelf-page {
                display: grid;
                grid-template-columns: repeat(3, 1fr);
            }
        }
    }

    /* Medium Devices, Desktops */
    @media only screen and (max-width : 992px) {
        main.page-category section.content-category {
           
            .wraper-category {
                display: grid;
                grid-template-columns: 1fr;
            }
        
            .shelf-page {
                display: grid;
                grid-template-columns: repeat(3, 1fr);
            }

          

            section.filters {
                display: none;
            }

            section.filters-mobile {
                display: block;
            }

            
                
            
        }

        .gateway {
            text-align: center;
        }
        
    }

    .filters {
        .filtering:nth-child(1) {
            header.more-filtering {
                svg.close {
                    display: block;
                } 

                svg.open {
                    display: none;
                }
            }
        }
    }

    .filters {
        .filtering {

            header.more-filtering {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                
                border-bottom: thin solid rgba($cor-box, 0.3);
                cursor: pointer;

                svg.close {
                    display: none;
                }
            }

            header.more-filtering {
                padding: 15px 0;
            }

            

            .list-filter.appear {
                display: block;
            }

            .list-filter {
                display: none;

                ul {
                    padding: 15px 0 0 0;
                }
            }
        }

        .filtering:first-child {
            header.more-filtering {
                padding-top: 0;
            }
        }

    }

    /* Small Devices, Tablets */
    @media only screen and (max-width : 768px) {
     
        main.page-category section.content-category {
            display: grid;
            grid-template-columns: 1fr;
        
            .shelf-page {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
            }
        }

        div.fix {
            margin: 20px 0;
        }
        
        div.fix div.per_page {
            display: flex;
            flex-direction: column;
            width: 50%;

            span {
                font-size: em(14);
                margin: 0 0 5px 0;
            }

            .nice-select {
                width: 100%;

                .list {
                    width: 100%;

                    .option {
                        width: 100%;
                    }
                }
            }
        }

        div.fix div.per_order {
            display: flex;
            flex-direction: column;
            width: 50%;

            span {
                font-size: em(14);
                margin: 0 0 5px 0;
            }

            .nice-select {
                width: 100%;

                .list {
                    width: 100%;

                    .option {
                        width: 100%;
                    }
                }
            }
        }
        
        
    }

    /* Extra Small Devices, Phones */
    @media only screen and (max-width : 480px) {

    }

    /* Custom, iPhone Retina */
    @media only screen and (max-width : 320px) {
        main.page-category section.content-category {
            display: grid;
            grid-template-columns: 1fr;
        
            .shelf-page {
                display: grid;
                grid-template-columns: repeat(1, 1fr);
            }

        }


    }