main.product-page section.content {
    display: grid;
    grid-template-columns: 50% 1fr;
    margin: 40px 0 0 0;
}

main.product-page section.content section.info {

    section.leading {
        header.name h2 {
            font-size: em(25);
            line-height: em(20);
            font-family: "Swiss 721 Bold";
            color: $cor-escrita;
        }

        article.briefing {
            p {
                margin: 12px 0;
            }

            a {
                display: block;
                color: $cor-primaria;
                font-family: "Swiss 721 Roman";
                transition: 0.1s ease-in-out;

                &:hover {
                    color: $cor-secundaria;
                }
            }
        }
    }

    section.find {
        margin: 30px 0 30px 0;
        width: 65%;
        background: rgba($cor-box, 0.5);
        padding: 20px;
        border-radius: 5px;
        display: grid;
        grid-template-columns: 60% 1fr;

        div.price {
            display: flex;
            flex-direction: column;
            justify-content: center;
            strong {
                color: $cor-secundaria;
                font-size: em(25);
                font-family: "Swiss 721 Bold";
                display: block;
            }

            span {
                font-size: em(13);
                display: block;
                color: $cor-escrita;
                margin: 5px 0 0 0;

            }
        }

        div.change {

            span {
                color: $cor-escrita;
                font-family: "Swiss 721 Roman";
                font-size: em(15);
                display: inline-block;
                margin-bottom: 10px;
            }

            .change-qty {
                background: #FFFFFF;
                display: inline-block;
                border-radius: 6px;

                input[type="number"] {
                    width: 50px;
                    border: none;
                    background-color: #FFFFFF;
                    outline: 0;
                    height: 45px;
                    appearance: textfield;
                    text-align: center;
                    font-size: em(18);
                    font-family: "Swiss 721 Roman";
                }

                input[type=number]::-webkit-inner-spin-button { 
                    -webkit-appearance: none;
                    
                }

                input[type="button"] {
                    background-color: #FFFFFF;
                    border: none;
                    color: $cor-escrita;
                    height: 45px;
                    width: 20px;
                    font-size: em(20);
                    cursor: pointer;
                }

                input[type="button"]:first-child {
                    border-top-left-radius: 6px;
                    border-bottom-left-radius: 6px;
                }

                input[type="button"]:last-child {
                    border-top-right-radius: 6px;
                    border-bottom-right-radius: 6px;
                }
            }
        }
    }

    button.get {
        width: 65%;
        height: 50px;
        border: none;
        border-radius: 15px;
        background-color: $cor-acao;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transition: 0.3s ease-in-out;

        &:hover {
            background: $cor-primaria;
        }

        a {
            text-transform: uppercase;
            color: #FFFFFF;
            font-size: em(23);
            font-family: "Swiss 721 Bold";
            display: block;
            height: 50px;
            line-height: 50px;
        }
    }

    section.shipping {
        margin: 40px 0 0 0;
        width: 65%;
        div.inquiry {
            display: flex;
            align-items: center;
            margin: 0 0 10px 0;
            span {
                margin: 0 0 0 7px;
                font-size: em(14);
                color: $cor-secundaria;
            }
        }

        div.calculate {
            display: flex;
            flex-direction: row;
            align-items: center;
            input[type="text"] {
                width: 70%;
                height: 50px;
                background-color: rgba($cor-box, 0.5);
                outline: 0;
                border: 0;
                padding: 10px;
                font-size: em(17);
                border-radius: 15px;
                font-family: "Swiss 721 Roman";
            }

            input[type="submit"] {
                width: 30%;
                border: 0;
                height: 40px;
                margin: 10px;
                border-radius: 15px;
                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                cursor: pointer;
                background-color: $cor-secundaria;
                color: #FFFFFF;
                font-size: em(17);
                font-family: "Swiss 721 Roman";
                transition: 0.3s ease-in-out;

                &:hover {
                    background: rgba($cor-secundaria, 0.7);
                }
            } 
        }
    }
}

main.product-page section.view {
    margin: 0 80px;

    section.products-view {
        div.image {
            img {
                width: 100%;
            }
        }
    }

    .slick-next,
    .slick-prev {
        z-index: 10;
        background-color: $cor-box;
        width: 38px;
        height: 38px;
        border-radius: 30px;

        svg {
            stroke: $cor-secundaria;
        }

        &:before {
            display: none;
        }
    }

    .slick-next {
        right: -50px;
    }

    .slick-prev {
        left: -50px;
    }

    .slick-dotted.slick-slider {
        margin-bottom: 50px;
    }

}

main.product-page .slick-dots {
    position: absolute;
    bottom: -45px;

    li.slick-active {
        button {
            &:before {
                background: $cor-secundaria;
            }
        }
    }

    li {
        button {
            &:before {
                width: 16px;
                height: 16px;
                opacity: 1;
                color: transparent;
                border: 2px solid $cor-secundaria;
                border-radius: 30px;
            }
        }
    }
}

.tabs {
    display: flex;
    flex-wrap: wrap;

    label {
        order: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 15px 50px;
        margin-right: 0.2rem;
        cursor: pointer;
        background-color: #fff;
        font-weight: bold;
        transition: background ease 0.3s;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        user-select: none;
        height: 60px;

        h2 {
            font-size: em(22);
        }
    }

    article.tab {
        order: 9;
        flex-grow: 1;
        width: 100%;
        display: none;
        padding: 30px 60px;
        background: #fff;
        background: rgba($cor-box, 0.5);

        p {
            font-size: em(16);
            color: $cor-escrita;
            font-family: "Swiss 721 Roman";
        }
    }

    input[type="radio"] {
        display: none;

        &:checked + label {
            background: rgba($cor-box, 0.5);
            border-top-left-radius: 20px;
            border-top-right-radius: 20px;
            display: block;
            position: relative;

            &:after {
                content: '';
                width: 80%;
                height: 3px;
                background: $cor-secundaria;
                position: absolute;
                left: 20px;
                bottom: 0;
            }
            

            & + .tab {
                display: block;
                min-height: 300px;
            }

            h2 {
                font-size: em(22);
                color: $cor-secundaria;

            }
        }
    }
}

   


