        /* Medium Devices, Desktops */
        @media only screen and (max-width : 992px) {
            main.product-page section.view {
                margin: 0 35px;

                .slick-next,
                .slick-prev {
                    width: 32px;
                    height: 32px;

                    svg {
                        width: 10px;
                        height: 14px;
                    }
                }

                .slick-next {
                    right: -40px;
                }
                
                .slick-prev {
                    left: -40px;
                }
            }

            main.product-page section.content {
                display: flex;
                flex-direction: column;
            }

            main.product-page section.content section.info section.find {
                width: 100%;
            }

            main.product-page section.content section.info button.get {
                width: 100%;
            }

            main.product-page section.content section.info section.shipping {
                width: 100%;
            }

            main.product-page .slick-dots {
                position: absolute;
                bottom: 0px;
            }

            main.product-page .tabs label {
                padding: 15px 25px;
                margin: 0;
                width: 50%;
            }

            main.product-page section.showcase.related {
                margin: 60px 0 60px;
            }

            main.product-page .tabs input[type=radio]:checked+label+.tab {
                min-height: auto;
            }

            

            .tabs input[type=radio]:checked+label h2,
            .tabs label h2 {
                font-size: em(20);
                text-align: center;
            }

            main.product-page section.content section.info section.find .change {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-end;
            }

            main.product-page section.content section.info section.find {
               height: 180px;
            }

            main.product-page section.content section.info section.find div.change span {
                font-size: em(22);
            }

            main.product-page section.content section.info section.find div.change .change-qty input[type=button] {
                height: 70px;
                width: 40px;
            }

            
            main.product-page section.content section.info section.find div.change .change-qty input[type=number] {
                height: 70px;
                width: 50px;
            }

            main.product-page section.content section.info section.find div.price {

                strong {
                    font-size: em(34);
                }

                span {
                    font-size: em(21);
                }
                
            }

        }

        /* Small Devices, Tablets */
        @media only screen and (max-width : 768px) {

            main.product-page section.content section.info section.find {
                height: auto;
                grid-template-columns: 1fr;
             }

             html main.product-page section.content section.info section.find .change {
                 display: flex;
                 flex-direction: column;
                 justify-content: center;
                 align-items: flex-start;
                 margin: 30px 0 0 0
             }

             main.product-page section.content section.info section.find div.change span {
                 font-size: em(18);
             }
             

             main.product-page section.content section.info section.find div.change .change-qty input[type=button] {
                height: 40px;
                width: 20px;
            }

            
            main.product-page section.content section.info section.find div.change .change-qty input[type=number] {
                height: 40px;
                width: 40px;
            }

            main.product-page section.content section.info section.find div.price {

                strong {
                    font-size: em(32);
                }

                span {
                    font-size: em(16);
                }
                
            }
            
            
        }

